body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-fluid{border:1px solid #f3f3f3;background:#f3f3f3}

.header-wrapper{text-align:center !important}h1,h2,h3,h4,h5,h6{font-family:"Open Sans",sans-serif;width:auto;font-weight:400;text-align:left;display:inline-block;margin:25px auto !important;padding:3px 0;position:relative}h1:after,h2:after,h3:after,h4:after,h5:after,h6:after{content:"";position:absolute;bottom:-10px;left:0;margin-left:0;width:15%;border-bottom-width:2px;border-bottom-style:solid;border-bottom-color:inherit}h1{font-size:350%}h2{font-size:300%}h3{font-size:250%}h4{font-size:200%}h5{font-size:150%}h6{font-size:100%}.normal-heading{color:#1e90ff}.error{color:red}

.form-wrapper{margin:30px 30px}.disability{border:1px solid #dee2e6;padding:15px 25px;margin:25px 0;border-radius:5px 5px 5px 5px;-webkit-border-radius:5px 5px 5px 5px;-moz-border-radius:5px 5px 5px 5px;-o-border-radius:5px 5px 5px 5px;-khtml-border-radius:5px 5px 5px 5px;background:#dee2e6}.form-group{margin:0 0 30px}

.results-wrapper{padding:0 45px}.table{margin-top:30px;margin-bottom:45px}.table tr td:last-child{text-align:right}.paye-results{font-weight:bold}

.cal-wrapper,.results-wrapper{border:1px solid #d3d3d3;background:#fff;max-width:750px;height:auto !important;border-radius:5px 5px 5px 5px;-webkit-border-radius:5px 5px 5px 5px;-moz-border-radius:5px 5px 5px 5px;-o-border-radius:5px 5px 5px 5px;-khtml-border-radius:5px 5px 5px 5px}.cal-wrapper{margin:30px auto}.results-wrapper{margin:0 auto 30px}

